p, .c-paragraph, .c-text-2 {
  font-size: 16px;
  line-height: 1.5;
}

.headline_card {
  font-size: 36px;
}

.c-text-1 {
  font-size: 18px;
}

.c-text-2 {
  font-size: 16px;
}

.c-text-3 {
  font-size: 15px;
}

.padding-sm {
  padding: clamp(1rem, 3%, 1.5rem);
}

.stack,
.c-flow {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.stack:only-child,
.c-flow:only-child {
  block-size: 100%;
}
.stack > * + *,
.c-flow > * + * {
  margin-block-start: var(--space, 1.5rem);
}

.stack-exception,
.stack-exception + * {
  --space: 3rem;
}

